export const ARRAY = 'array';
export const FLOAT = 'float';
export const STRING = 'string';
export const INTEGER = 'int';
export const BOOLEAN = 'bool';
export const INPUT_TYPES = {
  [ARRAY]: ARRAY,
  [FLOAT]: FLOAT,
  [STRING]: STRING,
  [INTEGER]: INTEGER,
  [BOOLEAN]: BOOLEAN,
};

const initialValues = {
  [ARRAY]: null,
  [FLOAT]: 0.0,
  [INTEGER]: 0,
  [BOOLEAN]: false,
  [STRING]: '',
};

export const getInitialValues = (type, defaultValue, initialVal) => {
  if (defaultValue) {
    return defaultValue;
  }
  if (!defaultValue && initialVal) {
    return initialVal;
  }
  return initialValues[type];
};
