<template>
  <v-switch
    :disabled="disabled"
    dense
    inset
    color="blue"
    v-model="_value"
    :rules="rules"
    :label="displayName"
    @change="v => onInput(v)"
  />
</template>
<script>
import validation from '../../common/validation';

export default {
  props: ['config', 'value'],
  data() {
    return {
      rules: [],
    };
  },
  computed: {
    displayName() {
      return (this.config && this.config.displayName) || '';
    },
    disabled() {
      return (this.config && this.config.disabled) || false;
    },
    _value: {
      get() {
        return this.value;
      },
      set(v) {
        return v;
      },
    },
  },
  mounted() {
    if (this.config) this.updateFieldRules();
  },
  methods: {
    onInput(v) {
      this.$emit('input', v);
    },
    updateFieldRules() {
      if (!this.config) {
        return;
      }
    },
  },
};
</script>
