<template>
  <v-card>
    <v-card-title class="mb-5">
      <span class="headline">{{ title }}</span>
    </v-card-title>
    <v-card-text>
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-row dense v-if="isFormConfigured">
          <v-col dense cols="12">
            <v-select
              dense
              v-model="modulePath"
              :rules="[rules.required]"
              :items="plugins"
              item-text="name"
              :disabled="isSelectPluginDisabled"
              item-value="module_path"
              label="Select Plugin"
              :loading="isFetchingSchema"
              @change="onPluginChange"
            ></v-select>
          </v-col>
          <v-col dense cols="12" :key="fieldName" v-for="fieldName of Object.keys(_configs)">
            <dynamic-form-field
              :config="_configs[fieldName]"
              :value="formValues[fieldName]"
              @input="val => onInputChange(fieldName, val)"
            ></dynamic-form-field>
          </v-col>
        </v-row>
        <v-spacer></v-spacer>
      </v-form>
    </v-card-text>
    <v-card-actions class="align-end justify-end">
      <v-btn v-if="onCancel" color="darken-1" text @click="onCancel"> Cancel </v-btn>
      <v-btn color="blue darken-1" text @click="onSubmit(formValues)" :loading="isSaving"> Save </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import DynamicFormField from './dynamicFormField.vue';
import { getInitialValues } from './input.constant';
import { extractDisplayName } from './formComponent.uiservice';
import validation from '../../common/validation';
import * as pluginService from '../../services/pluginService';
export default {
  name: 'dynamic-form',
  props: ['onCancel', 'onSave', 'title', 'initialFormValues', 'moduleName', 'isEdit', 'parentModulePath'],
  components: { DynamicFormField },
  data() {
    return {
      isSaving: false,
      modulePath: null,
      isFormConfigured: false,
      formValues: {},
      configs: {},
      valid: false,
      isFetchingSchema: false,
      rules: {
        required: validation.required,
      },
    };
  },
  mounted() {
    if (this.isEdit || this.parentModulePath) {
      this.modulePath = this.initialFormValues.plugin_name;
      this.onPluginChange(this.modulePath);
    }
    if (this.initialFormValues) {
      this.formValues = {
        ...this.initialFormValues,
      };
    }
    this.updateFormValues();
  },
  computed: {
    isSelectPluginDisabled() {
      return !!this.parentModulePath || this.isEdit;
    },
    plugins() {
      return this.$store.state.rfPlugins.filter(value => value && value.has_network && value.enabled);
    },
    _configs() {
      return Object.keys(this.configs || {}).reduce((acc, curr) => {
        acc[curr] = {
          ...this.configs[curr],
          displayName: extractDisplayName(this.configs[curr].display_name, curr),
          disabled: curr == 'plugin_name',
        };
        return acc;
      }, {});
    },
  },
  methods: {
    cleanUp() {
      this.formValues = {};
      if (typeof this.onCancel === 'function') this.onCancel();
    },
    onInputChange(fieldKey, value) {
      this.formValues[fieldKey] = value;
    },
    updateFormValues() {
      if (!this._configs) {
        return;
      }
      this.formValues = Object.keys(this._configs).reduce((acc, configKey) => {
        acc[configKey] = getInitialValues(
          this._configs[configKey].type,
          this.initialFormValues[configKey],
          this._configs[configKey].default,
        );
        return acc;
      }, {});
      this.isFormConfigured = true;
    },
    onPluginChange(modulePath) {
      if (!(modulePath && this.moduleName)) {
        return;
      }
      this.isFetchingSchema = true;
      pluginService
        .fetchSchema(modulePath, this.moduleName)
        .then(configs => {
          this.configs = configs;
          this.updateFormValues();
        })
        .finally(() => {
          this.isFetchingSchema = false;
        });
    },
    onSubmit() {
      if (!this.$refs.form.validate()) {
        return;
      }
      this.isSaving = true;
      this.formValues = {
        ...this.initialFormValues,
        ...this.formValues,
      };
      typeof this.onSave === 'function' &&
        this.onSave(this.formValues)
          .then(() => {
            this.$toasted.show(`Successfully ${this.isEdit ? 'updated' : 'created'} ${this.title}.`);
            this.cleanUp(true);
          })
          .catch(() => {
            this.$toasted.show(`Failed to ${this.isEdit ? 'update' : 'create'} ${this.title}.`, {
              type: 'error',
            });
          })
          .finally(() => {
            this.isSaving = false;
          });
    },
  },
};
</script>
