<template>
  <span>
    <array v-if="isArray" :value="_value" @input="val => onInput(val)" :config="config"></array>
    <boolean v-if="isBoolean" :value="_value" @input="val => onInput(val)" :config="config"></boolean>
    <string-field v-if="isString" :value="_value" @input="val => onInput(val)" :config="config"></string-field>
    <float-field v-if="isFloat" :value="_value" @input="val => onInput(val)" :config="config"></float-field>
    <integer-field v-if="isInteger" :value="_value" @input="val => onInput(val)" :config="config"></integer-field>
  </span>
</template>
<script>
import Array from './array.vue';
import Boolean from './boolean.vue';
import FloatField from './floatField.vue';
import StringField from './stringField.vue';
import IntegerField from './integerField.vue';

import { ARRAY, FLOAT, STRING, INTEGER, BOOLEAN } from './input.constant';
export default {
  name: 'dynamic-form-field',
  props: ['config', 'value'],
  components: { Array, Boolean, StringField, IntegerField, FloatField },
  computed: {
    _value() {
      return this.value;
    },
    type() {
      return (this.config && this.config.type) || null;
    },
    isArray() {
      return this.type === ARRAY;
    },
    isBoolean() {
      return this.type === BOOLEAN;
    },
    isString() {
      return this.type === STRING;
    },
    isInteger() {
      return this.type === INTEGER;
    },
    isFloat() {
      return this.type === FLOAT;
    }
  },
  methods: {
    onInput(value) {
      this.$emit('input', value);
    },
  },
};
</script>
