<template>
  <v-text-field
    dense
    type="number"
    :min="minVal"
    :max="maxVal"
    v-model="_value"
    :rules="rules"
    :items="options"
    :disabled="disabled"
    :step="1"
    @keydown="e => onIntegerKeyDown(e)"
    :label="displayName"
    :default="defaultValue"
    @input="v => onInput(v)"
  ></v-text-field>
</template>
<script>
import { INTEGER } from './input.constant';
import validation from '../../common/validation';

export default {
  name: 'integer-field',
  props: ['config', 'value'],
  data() {
    return {
      rules: [],
    };
  },
  computed: {
    displayName() {
      return (this.config && this.config.displayName) || '';
    },
    disabled() {
      return (this.config && this.config.disabled) || false;
    },
    _value: {
      get() {
        return this.value;
      },
      set(value) {
        return value;
      },
    },
    defaultValue() {
      return (this.config && this.config.default) || null;
    },
    options() {
      return (this.config && this.config.options) || [];
    },
    minVal() {
      return this.config && this.config.min ? this.config.min : -Infinity;
    },
    maxVal() {
      return this.config && this.config.max ? this.config.max : Infinity;
    },
  },
  mounted() {
    if (this.config) this.updateFieldRules();
  },
  methods: {
    onInput(v) {
      this.$emit('input', +v);
    },
    onIntegerKeyDown(e) {
      if (e.key === '.') {
        e.preventDefault();
      }
    },
    updateFieldRules() {
      if (!this.config) {
        return;
      }
      this.config.type === INTEGER && this.rules.push(validation.number);

      this.config.type === Boolean(this.config.required) && this.rules.push(validation.required);
    },
  },
};
</script>
