function capitalize(word) {
  return word
    .split('')
    .map((letter, index) => (index ? letter.toLowerCase() : letter.toUpperCase()))
    .join('');
}

export const extractDisplayName = (displayName, fieldKey) => {
  return !!displayName
    ? displayName
    : fieldKey
        .split('_')
        .map(word => capitalize(word))
        .join(' ');
};
