<template>
  <v-select
    dense
    :disabled="disabled"
    v-model="_value"
    :rules="rules"
    :items="options"
    :label="displayName"
    @input="v => onInput(v)"
  ></v-select>
</template>
<script>
import validation from '../../common/validation';

export default {
  props: ['config', 'value'],
  data() {
    return {
      rules: [],
    };
  },
  computed: {
    displayName() {
      return (this.config && this.config.displayName) || '';
    },
    _value: {
      get() {
        return this.value;
      },
      set(v) {
        return v;
      },
    },
    disabled() {
      return (this.config && this.config.disabled) || false;
    },
    defaultValue() {
      return (this.config && this.config.default) || null;
    },
    options() {
      return (this.config && this.config.options) || [];
    },
  },
  mounted() {
    if (this.config) this.updateFieldRules();
  },
  methods: {
    onInput(v) {
      this.$emit('input', v);
    },
    updateFieldRules() {
      if (!this.config) {
        return;
      }

      Boolean(this.config.required) && this.rules.push(validation.required);
    },
  },
};
</script>
